import GUN from 'goog:module$node_modules$gun$browser';
import 'goog:module$node_modules$gun$sea';

//var gun = GUN();
//var gun = GUN(['https://gun-manhattan.herokuapp.com/gun']);
//var gun = GUN('https://gun-relay.herokuapp.com/gun');
var gun = GUN({ peers: ['http:localhost:8001/gun'] });
var user = gun.user().recall({sessionStorage: true});

export var login = function(name, password) {
    user.auth(name, password, ({ err }) => {
        if (err) {
            starter.components.loginPage.stopLoading();
            alert(err);     
        } 
        else {
            gun.get("users");
            starter.components.loginPage.stopLoading();
            starter.components.loginPage.toMainPage();        
        }
    });
}

export var register = function(name, password) {
    user.create(name, password, ({ err }) => {
        if (err) {
            starter.components.registrationPage.stopLoading();
            alert(err);
        } 
        else {
            gun.get("users").set(name);
            alert("Account created successfully");
            starter.components.registrationPage.stopLoading();
            starter.components.registrationPage.toLoginPage();
        }
    });
    return true;
}    

export var logout = function() {
    user.leave()
    window.location.reload(true);
}


